<template>
  <div class="home">
    <div class="bg">
      <img src="../assets/home.jpg" alt="">
      <div class="button" @click="login"></div>
      <!-- <div class="button" @click="$router.push('/login')"></div> -->
    </div>

        <!--U盾使用演示-->
    <el-dialog
      v-el-drag-dialog
      title="e路通使用演示"
      width="800px"
      :modal="false"
      :close-on-click-modal="true"
      :close-on-press-escape="true"
      :visible.sync="dialogVisible">
      <img src="../assets/chadun.gif" alt="">
      <el-alert style="margin-top: 5px" type="info">友情提示：本教学系统【出纳】角色登录的统一密码为：123456，【主管】角色登录的统一密码为：654321</el-alert>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="checkCN">以出纳身份登录</el-button>
        <el-button type="primary" @click="checkZG">以主管身份登录</el-button>
      </span>
    </el-dialog>
    <!--windows安全-->
    <el-dialog
      v-el-drag-dialog
      title="windows安全"
      width="430px"
      :modal="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :visible.sync="checkVisible">
      <img src="../assets/bank.png" alt="">
      <span slot="footer" class="dialog-footer">
        <el-button @click="checkVisible = false">取 消</el-button>
        <el-button type="primary" @click="password">确 定</el-button>
      </span>
    </el-dialog>
    <!--密码验证-->
    <el-dialog
      v-el-drag-dialog
      title="中国建设银行网银盾"
      width="275px"
      :modal="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :visible.sync="passwordVisible">
      <div class="position-relative ">
        <img src="../assets/password.png" alt="">
        <el-input v-model="pwd" class="password-input" type="password"></el-input>
        <div class="password-button" @click="start"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import elDragDialog from '@/directive/el-drag-dialog' // base on element-ui
// @ is an alias to /src
export default {
  name: 'Home',
  directives: { elDragDialog },
  data () {
    return {
      dialogVisible: false,
      checkVisible: false,
      passwordVisible: false,
      pwd: ''
    }
  },
  methods: {
    login () {
      this.dialogVisible = true
    },
    // 出纳操作
    checkCN () {
      // 设置出纳信息
      const data = Object.assign(this.$store.state.userInfo)
      data.name = '出纳'
      data.password = '123456'
      this.$store.commit('setUserInfo', data)
      // 设置角色
      this.$store.commit('setRole', 1)
      this.dialogVisible = false
      this.checkVisible = true
    },
    // 主管操作
    checkZG () {
      // 设置主管信息
      const data = Object.assign(this.$store.state.userInfo)
      data.name = '主管'
      data.password = '654321'
      this.$store.commit('setUserInfo', data)
      // 设置角色
      this.$store.commit('setRole', 2)
      this.dialogVisible = false
      this.checkVisible = true
    },
    password () {
      this.checkVisible = false
      this.passwordVisible = true
    },
    start () {
      if (this.pwd.trim() === this.$store.state.userInfo.password) {
        this.$store.commit('changeLoginState', 1)
        this.$router.push('/nei_login')
      } else {
        this.$message.warning('密码错误')
      }
    }
  }
}
</script>
<style scoped lang="scss">
  .home {
    position: relative;
    text-align: center;
    width: 100%;
    height: auto;
    .bg {
      width: 1200px;
      height: auto;
      margin: 0 auto;
      position: relative;
      img {
        width: 100%;
        height: auto;
      }
      .button {
        position: absolute;
        left: 303px;
        top: 134px;
        width: 74px;
        height: 34px;
        cursor: pointer;
        background-color: transparent;
      }
    }

    .password-input {
      position: absolute;
      left: 170px;
      width: 91px;
      top: 29px;
      /deep/ .el-input__inner{
        height: 17px !important;
        border-radius: 0px;
        padding: 0px;
        border: none;
      }
    }
    .password-button {
      position: absolute;
      left: 50px;
      width: 75px;
      height: 25px;
      top: 65px;
      cursor: pointer;
    }
  }
</style>
